.example {
  background-color: #eaeaea;
  padding: 25px 20px 40px;

  @media (min-width: $tablet-width) {
    padding: 43px 30px 55px;
  }

  @media (min-width: $desktop-width) {
    padding: 26px 110px 65px;

  }

}

.example__heading {
  margin-top: 0;
  margin-bottom: 40px;
  font-family: $font-heading;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;

  @media (min-width: $tablet-width) {
    background-color: $white;
  }
}


.example__title {
  color: $special-dark-grey;
  font-family: $font-body;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
}

.example__description {
  font-family: $font-heading;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
}

.example__price-total {
  margin-top: 0;
  margin-bottom: 40px;
  font-family: $font-heading;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.example__images {
  margin-bottom: 15px;
}

.example__toggle {
  font-family: $font-heading;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
